<template>
  <div>
    <div class="filter-container">
      <button 
        class="filter-tab"
        :class="{ 'active': !displayFavorites }"
        @click="filterList('all')">All</button>
      <button
        class="filter-tab"
        :class="{ 'active': displayFavorites }"
        @click="filterList('fav')">Favorites</button>
    </div>
    <vue-good-table
      :rows="filteredList"
      :columns="columns"
      :searchOptions="searchOptions"
      :paginationOptions="paginationOptions">
      <div slot="emptystate">
        No results found...
      </div>
      <template slot="table-row" slot-scope="props">
        <span 
          v-if="props.column.field === 'status'" 
          :class="getStatusCellClass(props.row)"
        >
          {{props.formattedRow[props.column.field]}}
        </span>
        <span 
          v-else-if="props.column.html" 
          v-html="props.formattedRow[props.column.field]">
        </span>
        <span 
          class="favorite-cell"
          v-else-if="props.column.field === 'isFavorite'">
          <Favorite :row="props.row" @onFavoriteToggle="onFavoriteToggle"/>
        </span>
        <span v-else>
          {{props.formattedRow[props.column.field]}}
        </span>
      </template>
    </vue-good-table>
  </div>
</template>

<script>
import Favorite from '@/components/Favorite';

export default {
  name: 'List',
  components: {
    Favorite
  },
  props: {
    list: Array
  },
  computed: {
    filteredList() {
      return this.displayFavorites ?
        this.list.filter(item => item.isFavorite) :
        this.list;
    }
  },
  data() {
    return {
      displayFavorites: false,
      columns: [
        {
          label: '',
          field: 'isFavorite',
          width: '30px'
        },
        {
          label: 'Status',
          field: 'status'
        },
        {
          label: 'Public Key',
          field: 'publicKey',
          html: true,
        },
        {
          label: 'IP',
          field: 'ip'
        },
        {
          label: 'Last Seen',
          field: 'lastSeen'
        },
        {
          label: 'Last Paid',
          field: 'lastPaid'
        },
        {
          label: 'Active Since',
          field: 'activeSince'
        }
      ],
      searchOptions: {
        enabled: true,
        placeholder: 'Search masternodes',
        searchFn: this.searchFn
      },
      paginationOptions: {
        enabled: true,
        perPage: 50,
        perPageDropdown: [50, 100, 250, 500],
        position: 'both',
        dropdownAllowAll: true,
        rowsPerPageLabel: 'Per page',
      }
    }
  },
  methods: {
    getStatusCellClass(row) {
      return row.status === 'ENABLED' ? 'cl-success' : 'cl-warn';
    },
    onFavoriteToggle(row) {
      this.$emit('onFavoriteChanged', row.ip);
    },
    filterList(type) {
      this.displayFavorites = type === 'fav';
    },
    searchFn(row, col, cellValue, searchTerm) {
      return row.ip.indexOf(searchTerm) > -1 ||
        row.publicKey.indexOf(searchTerm) > -1;
    }
  },
  watch: {
    displayFavorites(newVal) {
      localStorage.setItem('vm.displayFavorites', newVal);
    }
  },
  created() {
    this.displayFavorites = JSON.parse(localStorage.getItem('vm.displayFavorites')) || false;
  }
};
</script>

