  &:after {
    content: '';
    display: table;
    clear: both;
  }

  .filter-tab {
    float: left;
    width: 50%;
    height: 40px;
    background-color: white;
    border: 1px solid#DCDFE6;
    border-bottom: none;
    font-family: inherit;
    font-size: 15px;
    cursor: pointer;

    &:nth-child(1) {
      border-right: none;
    }

    &:focus, &:active {
      outline: none;
    }

    &.active {
      background-color: lighten(#8fd16f, 30%);
    }
  }
}

.favorite {
  svg {
    width: 20px;
  }
}

.favorite-cell {
  display: block;
  text-align: center;
}

