import moment from 'moment';
import { isFavorite } from './favorites';

export const transformListData = (list) => {
  return Object.keys(list).map((hash) => {
    const [
      status, 
      port, 
      address, 
      ip, 
      lastPaid, 
      activeSeconds, 
      lastSeen
    ] = list[hash].trim().replace(/\s\s+/g, ' ').split(' ');

    return {
      status,
      publicKey: transformPublicKey(address),
      ip,
      lastSeen: moment(lastSeen * 1000).fromNow(),
      activeSince: moment(Date.now() - (activeSeconds * 1000)).fromNow(),
      lastPaid: moment(lastPaid * 1000).fromNow(),
      port,
      isFavorite: isFavorite(ip)
    };
  });
};

const transformPublicKey = (address) => {
  return `
    <a href="http://vantaur.chainlist.online/address/${address}" target="_blank">${address}</a>
  `;
};

