<template>
  <div id="app">
    <header>
      <img src="http://vantaur.tk/img/vtarft.png" />
      <h1>Masternodes</h1>
    </header>
    <p v-if="loading">Loading...</p>
    <p v-else-if="!list.length">Something bad happened! :( </p>
    <list v-else :list="list" @onFavoriteChanged="onFavoriteChanged"/>
  </div>
</template>

<script>
import axios from 'axios';

import List from '@/components/List';
import { transformListData } from '@/lib/transformer';
import { toggleItemInFavorites } from '@/lib/favorites';

export default {
  name: 'app',
  components: {
    List
  },
  data() {
    return {
      loading: true,
      count: null,
      list : [],
      errors: []
    }
  },
  methods: {
    async getListCount() {
      try {
        const { data } = await axios.get('/mncount.txt');

        return data;
      } catch (e) {
        throw new Error('Error while getting masternodes count!');
      }
    },
    async getList() {
      try {
        const { data } = await axios.get('/mnlist.txt');
        
        return transformListData(data);
      } catch (e) {
        throw new Error('Error while getting masternode list!');
      }
    },
    async syncData() {
      this.loading = true;
      try {
        this.count = await this.getListCount();
        this.list = await this.getList();
      } catch (error) {
        this.errors.push(error);
      }
      this.loading = false;
    },
    onFavoriteChanged(ip) {
      const item = this.list.find((item) => item.ip === ip);
      
      item.isFavorite = !item.isFavorite;
      toggleItemInFavorites(item.ip);
    }
  },
  mounted() {
    this.syncData();
  }
}
</script>

