const LS_KEY = 'vm.favorites';

const getFavorites = () => {
  return JSON.parse(localStorage.getItem(LS_KEY)) || [];
}

const saveFavorites = (favorites) => {
  return localStorage.setItem(LS_KEY, JSON.stringify(favorites));
}

export const toggleItemInFavorites = (item) => {
  const favs = getFavorites();
  const index = favs.indexOf(item);

  if (index < 0) {
    favs.push(item);
  } else {
    favs.splice(index, 1);
  }

  saveFavorites(favs);
}

export const isFavorite = item => getFavorites().indexOf(item) > -1;
